.button {
  min-width: 130px;
  padding: 8px 24px;
  border-radius: 3px;
  border: none;
  font-size: 14px;
  font-weight: 600;

}

.disabled {
  background-color: rgba(0, 77, 153, 1);
  color: rgba(0, 46, 92, 1);
  opacity: 50%;
}

.secondary {
  border: 1px solid rgba(0, 102, 204, 1);
  color: rgba(0, 102, 204, 1);
  background-color: inherit;
}

.default {
  background-color: rgba(0, 102, 204, 1);
  color: rgba(255, 255, 255, 1);
}

.red {
  background-color: rgba(204, 20, 57, 1);
  color: rgba(255, 255, 255, 1);

}

.default:hover {
  background-color: rgba(0, 128, 255, 1);
}

.red:hover {
  background-color: rgba(255, 26, 71, 1);
}

.secondary:hover {
  background-color: rgba(229, 240, 250, 1);
}


.default:active {
  background-color: rgba(0, 89, 178, 1);
}

.secondary:active {
  background-color: rgba(201, 229, 255, 1);
}

.red:active {
  background-color: rgba(178, 18, 50, 1);
}

.button:disabled {}