.greetingForm {
  /* width: 100vw; */
  height: 200px;
  width: 500px;

}

.inputAndButtonContainer {
  display: flex;
  gap: 10px;
  padding: 10px 0;
}

.error {
  color: red;
}

.input {
  width: 372px;
  height: 36px;
  font-size: 16px;
  padding: 5px;
  border-radius: 5px;

}

.errorInput {
  width: 372px;
  height: 36px;
  font-size: 16px;
  padding: 5px;
  border-color: red;
  border-radius: 5px;
}
.input:focus {
  border-color: none;
}

.button {
  background-color: rgba(0, 102, 204, 1);
  width: 78px;
  height: 36px;
  border: none;
  color: white;
  border-radius: 5px;

}

.button:focus {
  background-color: rgb(29, 134, 239);

}

.button:active {
  background-color: rgb(56, 140, 224);

}

.button:disabled {
  background-color: rgb(184, 214, 243);
}

.text {
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  line-height: 17px;
}

.greeting {
  font-size: 14px;
  font-weight: 500;
  color: rgba(0, 0, 0, 1);
}
